#header-main-div {
	width: 100%;
	height: 60px;
	padding: 5px 0px;
	display: flex;
	flex-direction: row-reverse;
	justify-content: space-between;
	align-items: center;
}

.toggle-theme-text:hover {
	cursor: pointer;
}
.toggle-theme-text-light:hover {
	color: rgba(0, 0, 0, .7);
}
.toggle-theme-text-dark:hover {
	color: rgba(255, 255, 255, .9);
}

.nav-links-div ul {
	display: flex;
	justify-content: center;
	list-style-type: none;

	/* TODO: Ici, il faudra refactoriser les composants Header, Todos et About
	pour que tout s'affiche comme il faut.
	Le code ci-dessous est un hack.
	 */
	margin-left: -100px;
}
.nav-links-div li {
	margin: 0 10px;
}

.navlink {
	text-transform: uppercase;
	text-decoration: none;
	font-family: 'Lucida Sans', sans-serif;
	font-size: 16px;
}
.navlink-light {
	color: black;
}
.navlink-light:hover {
	color: #F4C20D;
}
.navlink-dark {
	color: white;
}
.navlink-dark:hover {
	color: #123473;
}


