.about-section {
	width: 100%;
	padding: 0 20px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

/* Cette règle CSS permet d'avoir un écart de contenu, entre
la bordure supérieure du contenu (content-div) et le premier élément de contenu,
similaire à celui de la page Todos.
TODO: A améliorer éventuellement
*/
.about-section:first-child {
	margin-top: -10px;
}
.about-section * {
	font-family: Raleway, sans-serif;
}
.about-section .about-section-title {
	font-size: 35px;
	font-weight: 700;
	font-family: 'Segoe UI', sans-serif;
	letter-spacing: -1px;
	margin-bottom: 0;
	text-transform: uppercase;
}
.about-section ul {
	list-style-type: none;
}
.about-section li {
	font-size: 16px;
	margin-bottom: 5px;
}
.link-item > a {
	text-decoration: none;
}
.link-item > a:hover {
	cursor: pointer;
	color: #4285F4;
}

.about-section-light * {
	color: black;
}
.about-section-dark * {
	color: #d3d3d3;
}
