:root {
	--primary-title-color-light: #707070;
	--success-color: #3CBA54;
	--warning-color: #F4C20D;
	--danger-color: crimson;
	--action-btn-scale-factor: 1.5;
	--border-radius: 5px;
}

/* --- Bouton d'ajout d'une todo --- */
.btn-add {
  font-size: 40px;
	font-weight: bold;
  width: 40px;
  height: 40px;
	border: none;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 15px;
	transition: transform 200ms linear;
}
.btn-add-light {
	color: #F0F0F0;
	background-color: #4285F4;
	box-shadow: 3px 5px 7px #ded8d8;
}
.btn-add-dark {
	color: #1b1b32;
	background-color: #d3d3d3;
	box-shadow: 3px 5px 7px black;
}

.btn-add:hover {
  cursor: pointer;
  transform: scale(1.3);
}

/* --- Liste des todos --- */
#todos-list {
	width: 100%;
	height: 100%;
	padding: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

#no-todos-div {
	width: 100%;
	height: 100%;
	font-size: 30px;
	font-weight: 900;
	font-family: Raleway, sans-serif;
	color: var(--primary-title-color-light);
	display: flex;
	justify-content: center;
	align-items: center;
}

/* --- Input pour l'ajout d'une nouvelle todo --- */
.add-todo-input {
  width: 50vw;
  height: 40px;
	font-size: 16px;
	font-family: Raleway, sans-serif;
	padding: 7px;
  border: none;
  border-radius: var(--border-radius);
}
.add-todo-input-light {
	color: black;
	background-color: white;
	box-shadow: 3px 5px 7px #ded8d8;
}
.add-todo-input-light:hover {
	border: 1px solid lightgrey;
}
.add-todo-input-dark {
	color: #d3d3d3;
	/* background-color: #1b1b32; */ /* DELETE: */
	background-color: #123473;
	box-shadow: 3px 5px 7px black;
}
.add-todo-input-dark:hover {
	border: 1px solid #232425;
}

/* --- Elément todo --- */
.todo-item-div {
	height: 50px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0px 10px;
	margin-bottom: 10px;
	border-radius: var(--border-radius);
  transition: box-shadow 300ms linear;
}
.todo-item-div-light {
	background-color: white;
}
.todo-item-div-light:hover {
	box-shadow: 3px 5px 7px #ded8d8;
}
.todo-item-div-dark {
	/* background-color: #1b1b32; */ /* DELETE: */
	background-color: #123473;

}
.todo-item-div-dark:hover {
	box-shadow: 3px 5px 7px black;
}

.todo-item-text {
	font-family: Raleway;
	font-size: 16px;
}
.todo-item-text-light {
	color: black;
}
.todo-item-text-dark {
	color: #d3d3d3;
}

/* Responsive design pour l'élément todo */
@media screen and (max-width: 576px) {
	.todo-item-div {
		width: 70vw;
	}
}
@media screen and (min-width: 576px) {
	.todo-item-div {
		width: 70vw;
	}
}
@media screen and (min-width: 768px) {
	.todo-item-div {
		width: 50vw;
	}
}
@media screen and (min-width: 992px) {
	.todo-item-div {
		width: 40vw;
	}
}
@media screen and (min-width: 1400px) {
	.todo-item-div {
		width: 30vw;
	}
}

/* --- Boutons d'action des éléments todo --- */
.todo-item-update-button,
.todo-item-delete-button,
.todo-item-toggle-complete-button {
	margin: 0 5px;
	font-size: 16px;
	transition: transform 150ms linear;
}

.todo-item-update-button:hover,
.todo-item-delete-button:hover,
.todo-item-toggle-complete-button:hover {
	cursor: pointer;
	transform: scale(var(--action-btn-scale-factor));
}

.todo-item-delete-button:hover {
	color: var(--danger-color);
}

.todo-item-update-button:hover {
	color: var(--warning-color);
}

.todo-item-toggle-complete-button:hover {
	color: var(--success-color)
}

/* --- Quand il n'y a pas de todos --- */
#no-todos-div {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
#no-todos-text {
	letter-spacing: -1px;
	font-size: 25px;
	margin-top: 15px;
}

/* --- Toutes les règles en-dessous de ce commentaire sont à réviser. --- */
#add-edit-todo-div {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 15px;
}

#add-edit-todo-textfield {
	height: 20px;
	width: 350px;
	font-size: 19px;
}
